@import url("https://fonts.googleapis.com/css?family=Arvo|Rubik:400,400i,700");
:root {
  --serif: 'Arvo', serif;
  --sans: 'Rubik', sans-serif;
  --blue: #355263;
  --blue-dark: #1F313F;
  --yellow: #FDD24E;
  --white: #EFEFEF;
  --grey: #D5D5D3; }

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

a:link,
a:visited {
  text-decoration: none;
  color: inherit; }

button {
  border: none;
  color: inherit;
  background: none; }

img {
  max-width: 100%; }

html {
  min-height: 100vh;
  font-size: 62.5%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: var(--sans); }

.App {
  background-color: var(--grey);
  height: 100vh;
  display: -ms-flexbox;
  display: flex; }

.sidebar {
  height: 100vh;
  width: 8rem;
  background-color: var(--blue);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between; }

.sidebar .logo {
  width: 4.8rem;
  height: 4.8rem;
  margin-top: 2rem; }

.sidebar .brand {
  -webkit-transform: rotate(-90deg) translateX(46%);
      -ms-transform: rotate(-90deg) translateX(46%);
          transform: rotate(-90deg) translateX(46%);
  white-space: nowrap;
  line-height: 8rem;
  color: var(--yellow);
  font-size: 2.4rem;
  letter-spacing: 3px;
  font-weight: normal;
  font-family: var(--sans);
  text-transform: uppercase; }

.container {
  height: 100vh;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  -ms-flex-preferred-size: calc(50vw - 4rem);
      flex-basis: calc(50vw - 4rem);
  background-color: #fff;
