  background-color: var(--white); }

.container #editor {
  width: 95%;
  height: calc(100vh - 14rem);
  resize: none;
  border: none;
  margin: 10rem 2.5% 0;
  padding: 2rem 2.5% 4rem;
  border-left: 0.5rem solid var(--grey); }

.container .label {
  position: absolute;
  color: var(--blue);
  background-color: var(--yellow);
  border-radius: .5rem;
  top: 2rem;
  left: 10%;
  height: 4.8rem;
  width: 80%;
  text-align: center;
  text-transform: uppercase;
  font-size: 1.8rem;
  line-height: 4.8rem;
  font-family: var(--sans);
  font-weight: normal;
  letter-spacing: 1px; }

.container:last-child {
  border-left: 1px solid var(--grey); }

.preview {
  width: 95%;
  height: calc(100vh - 14rem);
  margin: 10rem 2.5% 0;
  padding: 2rem 2.5% 4rem;
  font-family: var(--sans);
  font-size: 1.6rem;
  line-height: 1.6;
  color: var(--blue-dark);
  overflow: auto; }

.preview h1, .preview h2, .preview h3, .preview h4, .preview h5, .preview h6 {
  margin-top: 24px;
  margin-bottom: 16px;
  font-weight: 600;
  line-height: 1.25; }

.preview h1 {
  font-size: 2em;
  padding-bottom: .3em;
  border-bottom: 1px solid var(--grey); }

.preview h2 {
  font-size: 1.5em; }

.preview h3 {
  font-size: 1.2em; }

.preview a {
  color: #0366d6; }

.preview a:hover {
  text-decoration: underline; }

.preview p {
  margin-bottom: 1em; }

.preview blockquote {
